import { createStore } from 'vuex'

export default createStore({
  state: {
    darkmode: false
  },
  getters: {
  },
  mutations: {
    changeMode(state) {
      state.darkmode = !state.darkmode
      if(state.darkmode) {
        document.body.classList.add('dark-mode');
      } else {
        document.body.classList.remove('dark-mode');
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
