<template>
    <nav class="main-nav">
      <div class="nav-hamburger" v-if="mobileView">
        <font-awesome-icon icon="fa-solid fa-bars" class="icon" @click="toggleNav"/>
      </div>
      <!---------------------------------------------------------------------------------------->
      <div class="nav-menu" v-if="showNav">
          <router-link :to="item.link" class="nav-text" v-for="item in items" :key="item.id">
            {{item.name}}
          </router-link > 
      </div>
      <!---------------------------------------------------------------------------------------->
      <div class="nav-toggle-btn" v-if="showNav">
        <SwitchMode />
      </div>
  </nav>
</template>

<script>

import SwitchMode from '../components/SwitchMode.vue'

export default {
    name: "Navigation",
    components: {
      SwitchMode
    },
    data() {
      return{
        items: [
          {
            id: 1,
            name: "Home",
            link: "/",
          },
           {
            id: 2,
            name: "About",
            link: "/about",
          },
           {
            id: 3,
            name: "Projects",
            link: "/projects",
          },
           {
            id: 4,
            name: "Certifications",
            link: "/certifications",
          },

        ],
        mobileView: false,
        showNav: true,
        mobileWidth: 750
      }
    },
    methods: {
       handleView( ) {
            this.mobileView = window.innerWidth <= this.mobileWidth
            if(window.innerWidth > this.mobileWidth) {
                this.showNav = true
                this.mobileView = false
            } else {
              this.showNav = false
              this.mobileView = true
            }
        },
        toggleNav() {
            this.showNav = !this.showNav
        }
    },
    created() {
        window.addEventListener("resize", this.handleView)
    },
    unmounted() {
        window.removeEventListener("resize", this.handleView)
    },
}
</script>

<style scoped lang="scss">
    .main-nav {
        min-height: 70px;
        width: 100%;
        padding: .5rem 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
        background-color: var(--color-secondary);
    }

    .nav-hamburger {
      padding: 1rem 2rem;
      .icon {
        font-size: 2.5rem;
      }
    }

    .nav-menu, .nav-toggle-btn {
      transition: var(--transition-primary);
    } 
    .nav-menu {
      flex: 3;
      display: flex;
      justify-content: space-around;
      .nav-text {
        color: var(--color-grey-5);
        font-size: 1.4rem;
        transition: var(--transition-primary);
      }
      .nav-text:hover {
        color: var(--color-grey-2);
      }
    }

    .nav-toggle-btn {
      flex: 1;
      display: flex;
      justify-content: center;
    }

    @media screen and (max-width: 750px) {
      .main-nav {
          justify-content: space-between;
          flex-direction: column;
      }

      .nav-hamburger {
          align-self: start;
          order: 1;
      }
       .nav-menu {
          flex: 0;
          flex-direction: column;
          order: 3;
      }

      .nav-toggle-btn {
          flex: 0;
          order: 2;
          width: 100%;
      }

      .nav-menu {
          width: 100%;
          align-items: center;
          .nav-text {
            padding: 1.2rem 0;
          }
      }
    }

</style>
